.navbar-wrapper {
  width: 100%;
  height: 80px;
  font-family: "Didact Gothic", sans-serif;
  border-bottom: 1px solid #a9b1d3; }
  .navbar-wrapper .navbar-logo {
    margin: 20px 0 0 0;
    float: left; }
  .navbar-wrapper .navbar-tabs {
    float: right; }
    .navbar-wrapper .navbar-tabs .navbar-tabitem {
      display: inline-block;
      margin: 30px 0 auto 30px;
      text-decoration: none;
      font-size: 20px;
      font-weight: 300;
      color: #273c92; }
    .navbar-wrapper .navbar-tabs .navbar-tabitem-active {
      display: inline-block;
      margin: 30px 0 auto 30px;
      text-decoration: none;
      font-size: 20px;
      font-weight: 300;
      color: #f15c22; }

.content-wrapper {
  width: 100%; }
  .content-wrapper .map {
    width: 100%;
    height: 600px;
    position: relative; }
  .content-wrapper .control {
    z-index: 1;
    position: absolute;
    left: 10px;
    top: 60px; }
  .content-wrapper .selector {
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
    .content-wrapper .selector .handler {
      position: absolute;
      height: 12px;
      width: 12px;
      background-color: white;
      outline: solid 2px #273c92;
      cursor: move; }
    .content-wrapper .selector .shader {
      background-color: rgba(0, 0, 0, 0.15); }
  .content-wrapper .proceed-button {
    margin: 18px 0;
    float: right; }
  .content-wrapper .proceed-alert {
    float: right;
    font-weight: 300;
    margin: 18px;
    line-height: 50px; }
    .content-wrapper .proceed-alert.red {
      color: red; }
    .content-wrapper .proceed-alert.green {
      color: green; }
  .content-wrapper .row {
    width: 100%; }
  .content-wrapper .raw {
    border-top: 1px solid #a9b1d3;
    padding: 24px 0; }

.menu {
  float: left;
  font-family: "Didact Gothic", sans-serif;
  color: #273c92;
  width: 230px;
  margin: 12px 0 36px 0; }
  .menu .menu-item {
    width: 100%;
    font-size: 24px;
    cursor: pointer;
    height: 50px; }
    .menu .menu-item.selected {
      background-color: #273c92;
      color: white; }
      .menu .menu-item.selected:hover {
        background-color: #273c92; }
    .menu .menu-item:hover {
      background-color: #d4d8e9; }
    .menu .menu-item .menu-title {
      margin: 0 0 0 16px;
      float: left;
      line-height: 50px; }
  .menu .submenu {
    width: 100%; }
    .menu .submenu span {
      float: left; }
  .menu .submenu-item {
    height: 75px;
    width: 100%;
    font-size: 20px; }
    .menu .submenu-item .submenu-title {
      margin: 0 0 0 42px;
      float: left;
      line-height: 50px; }
    .menu .submenu-item .slider input {
      float: right;
      margin-right: 16px; }
    .menu .submenu-item .slider .slider-value {
      font-size: 16px;
      float: left;
      margin-left: 42px; }
    .menu .submenu-item .dragHandle {
      height: 20px;
      width: 20px;
      background-color: black; }
  .menu .icon {
    cursor: pointer;
    height: 18px;
    float: right;
    margin: 16px 16px;
    fill: #273c92; }
    .menu .icon.selected {
      fill: white; }

.maps {
  width: 100%; }
  .maps .functions {
    width: 100%;
    margin: 12px 0; }
  .maps .back {
    width: 230px;
    float: left; }
  .maps .back-icon {
    display: inline-block;
    height: 20px;
    margin: 16px 12px;
    fill: #273c92;
    float: left; }
  .maps .export-button {
    float: right; }
  .maps .map-display {
    margin: 12px 0 50px 0;
    width: calc(100% - 270px);
    float: right;
    position: relative;
    min-height: 500px; }

body {
  margin: 0; }

.content {
  width: 83.3333%;
  margin: 0 auto;
  font-family: "Roboto", sans-serif; }

.title {
  margin: 10px 0;
  display: inline-block;
  font-family: "Didact Gothic", sans-serif;
  font-size: 24px;
  color: #273c92;
  text-decoration: none; }

.button {
  display: inline-block;
  height: 50px;
  line-height: 50px;
  background-color: #273c92;
  padding: 0 20px;
  color: white;
  font-family: "Didact Gothic", sans-serif;
  font-size: 24px;
  text-decoration: none;
  transition: all .15s ease-in-out; }
  .button:hover {
    background-color: #5263a8; }
  .button.button-disabled {
    background-color: rgba(0, 0, 0, 0.4); }

.clearfix {
  clear: both; }

.footer {
  width: 100%;
  border-top: 1px solid #a9b1d3;
  padding: 24px 0;
  color: rgba(0, 0, 0, 0.64);
  font-weight: 300;
  font-size: 12; }

